@charset "UTF-8";
body {
  background-color: #2A3947 !important;
  min-height: 100vh;
  min-width: 100vw;
  font-family: Rosario;
  color: white;
  font-size: 1rem;
  cursor: default;
  text-align: justify;
  text-justify: inter-word;
}
body a {
  color: #fe8181;
  font-weight: 800;
}
body a:hover {
  color: #ffcc67fb;
  font-weight: 900;
  text-decoration: underline;
}
body .link-no-style {
  color: white;
}

.footer {
  background-color: #1E2A35 !important;
  margin: 2em 0 0 0;
  padding: 1rem !important;
  border-top: 1px solid #c8c8c8;
  color: white;
}
.footer .footer-text {
  font-size: 1.2 rem;
  font-family: Nunito Sans;
}
.footer a {
  max-width: 120px !important;
  display: inline-block;
  text-decoration: underline;
  color: #ffcc67fb;
}
.footer .social-media {
  color: white;
}
.footer .social-media i {
  font-size: 2rem;
  margin: 0.5rem;
}

/* -------------------------------------------------------------------------- */
/*                               global elements                              */
/* -------------------------------------------------------------------------- */
.clean-link {
  color: white;
}
.clean-link:hover {
  color: white;
  text-decoration: none;
}

.name-txt {
  font-family: Cormorant Upright;
}

.highlight, .highlight-green, .highlight-salmon, .highlight-lightblue, .highlight-black, .highlight-white, .highlight-darker {
  font-weight: 550;
  padding: 0.05em 0.2em;
  margin: 0.1em 0.1em;
}

.highlight-salmon {
  box-shadow: inset 0 -0.01em #2A3947, inset 0 -0.2em 0 0 #ff6d42;
  text-shadow: -1px -1px #2A3947, -1px 1px #2A3947, 1px -1px #2A3947, 1px 1px #2A3947;
}

.highlight {
  box-shadow: inset 0 -0.01em #2A3947, inset 0 -0.2em 0 0 rgba(255, 187, 52, 0.9843137255);
  text-shadow: -1px -1px #2A3947, -1px 1px #2A3947, 1px -1px #2A3947, 1px 1px #2A3947;
}

.highlight-green {
  box-shadow: inset 0 -0.01em #2A3947, inset 0 -0.2em 0 0 #42de49;
  text-shadow: -1px -1px #2A3947, -1px 1px #2A3947, 1px -1px #2A3947, 1px 1px #2A3947;
}

.highlight-lightblue {
  box-shadow: inset 0 -0.01em #2A3947, inset 0 -0.2em 0 0 #3fa9ff;
  text-shadow: -1px -1px #2A3947, -1px 1px #2A3947, 1px -1px #2A3947, 1px 1px #2A3947;
}

.highlight-darker {
  background-color: rgba(28, 39, 50, 0.5) !important;
  box-shadow: inset 0 -0.01em #2A3947, inset 0 -0.2em 0 0 #0f1d2a;
  text-shadow: -1px -1px #2A3947, -1px 1px #2A3947, 1px -1px #2A3947, 1px 1px #2A3947;
  color: white !important;
}

.highlight-black {
  color: black !important;
}

.highlight-white {
  color: white !important;
}

.textaccent {
  color: #ffcc67fb !important;
  font-weight: 900;
  text-decoration: underline;
}

.text-green {
  color: #6bce70 !important;
  font-weight: 900;
  text-decoration: underline;
}

.text-paragraph {
  margin-top: 0.5em;
  font-family: Nunito Sans;
  font-size: 1.3rem;
  color: white;
}

.sep-line {
  height: 3px;
  border-radius: 3px;
  background-color: #c8c8c8;
  width: 100%;
}

.fancy-title {
  font-family: DM Serif Display;
  font-size: 4rem;
  color: white;
  margin-left: 2rem;
  margin-top: 1.5em;
  font-weight: 900;
  display: inline-block;
}
@media screen and (max-width: 600px) {
  .fancy-title {
    font-size: 1.75rem;
    margin-left: 12px;
  }
}

.img-and-text {
  width: 80%;
  margin: 2em auto;
}
.img-and-text img {
  max-width: 250px;
  max-height: 250px;
  border-radius: 10px;
  display: inline-block;
  vertical-align: middle;
}
.img-and-text p {
  display: inline-block;
  vertical-align: top;
  font-size: 1.3rem;
  vertical-align: middle;
  margin: 0 3rem;
  width: calc(80% - 250px);
}
@media screen and (max-width: 600px) {
  .img-and-text {
    width: 95%;
    margin: 2em auto;
  }
  .img-and-text img {
    max-width: 100%;
    max-height: 100%;
    border-radius: 10px;
    display: block;
    margin: 0 auto;
  }
  .img-and-text p {
    display: block;
    vertical-align: top;
    font-size: 1.3rem;
    vertical-align: middle;
    margin: 0 0;
    width: 100%;
  }
}

.img-bg-white {
  background-color: white !important;
}

.float-right {
  float: right;
  margin-left: 4em;
  margin-right: 4em;
  display: flex;
  flex-direction: row;
  justify-content: right;
}

.float-left {
  float: left;
  margin-left: 4em;
  margin-right: 4em;
  display: flex;
  flex-direction: row;
  justify-content: left;
}

.center-img-text-around {
  position: relative;
  text-align: center;
  padding: 2em;
}
.center-img-text-around img {
  display: block;
  margin: 2em auto 2em auto;
  width: 20%;
  box-shadow: 0 0 1000px 20px rgba(255, 255, 255, 0.1);
}
.center-img-text-around .top-left {
  position: absolute;
  top: 0;
  left: 0;
  margin-left: 2em;
  padding-right: 2em;
  margin-top: 1em;
  text-align: left;
}
.center-img-text-around .bottom-right {
  position: absolute;
  bottom: 0;
  right: 0;
  margin-right: 2em;
  padding-left: 2em;
  margin-bottom: 1em;
  text-align: right;
}
.center-img-text-around .top-right {
  position: absolute;
  top: 0;
  right: 0;
  margin-right: 2em;
  padding-left: 2em;
  margin-top: 1em;
  text-align: right;
}
.center-img-text-around .bottom-left {
  position: absolute;
  bottom: 0;
  left: 0;
  margin-left: 2em;
  margin-bottom: 1em;
  padding-right: 2em;
  text-align: left;
}
.center-img-text-around p {
  font-size: 1.5rem;
  color: white;
  margin: 0.5em 0 0.5em 0;
  width: 40% !important;
}
@media screen and (max-width: 900px) {
  .center-img-text-around img {
    width: 50%;
    margin: 2em auto 2em auto;
  }
  .center-img-text-around p {
    font-size: 1.2rem;
    margin: 0.5em 0 0.5em 0;
    width: 95% !important;
  }
  .center-img-text-around .top-left {
    position: relative;
    margin: 0;
    margin-top: 1em;
    padding: 0;
    text-align: center;
  }
  .center-img-text-around .bottom-right {
    position: relative;
    margin: 0;
    margin-top: 1em;
    padding: 0;
    text-align: center;
  }
  .center-img-text-around .top-right {
    position: relative;
    margin: 0;
    margin-top: 1em;
    padding: 0;
    text-align: center;
  }
  .center-img-text-around .bottom-left {
    position: relative;
    margin: 0;
    margin-top: 1em;
    padding: 0;
    text-align: center;
  }
}

.narrow-30 {
  width: 30%;
}

.narrow-50 {
  width: 50%;
}

.narrow {
  width: 80%;
}

.narrow, .narrow-30, .narrow-50 {
  margin-left: auto !important;
  margin-right: auto !important;
  max-width: 1200px !important;
}
@media screen and (max-width: 600px) {
  .narrow, .narrow-30, .narrow-50 {
    width: 85% !important;
    margin: 0 auto !important;
  }
}

.unselectable {
  cursor: default;
}

.vert-on-mobile {
  display: flex;
  flex-direction: row;
  justify-content: right;
  align-items: left;
}
.vert-on-mobile span {
  margin-right: 0.5em;
}
@media screen and (max-width: 600px) {
  .vert-on-mobile {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}

.note-white, .note-salmon,
.note-green, .note-lightblue, .note-highlight {
  margin: 2em auto 2em auto !important;
  border-radius: 15px;
  height: fit-content;
  padding: 1em 2em;
  color: white;
}
.note-white .note-title, .note-salmon .note-title,
.note-green .note-title, .note-lightblue .note-title, .note-highlight .note-title {
  font-size: 1.5rem;
  font-weight: 900;
  font-family: Nunito Sans;
  margin-top: -1.5em;
  background-color: #2A3947 !important;
  width: fit-content !important;
  padding: 0 12px !important;
}
@media screen and (max-width: 600px) {
  .note-white .note-title, .note-salmon .note-title,
.note-green .note-title, .note-lightblue .note-title, .note-highlight .note-title {
    font-size: 1.2rem !important;
    margin-top: -1.6em;
  }
}
.note-white p, .note-salmon p,
.note-green p, .note-lightblue p, .note-highlight p {
  padding: 0 !important;
  color: white;
}

.note-white {
  border: 2px solid #b4b4b4;
}
.note-white .note-title {
  color: white !important;
}

.note-salmon {
  border: 2px solid #fb835f;
}
.note-salmon .note-title {
  color: #fb835f !important;
  float: right;
}

.note-green {
  border: 2px solid #6bce70;
}
.note-green .note-title {
  color: #6bce70 !important;
}

.note-lightblue {
  border: 2px solid #58b4ff;
}
.note-lightblue .note-title {
  color: #58b4ff !important;
}

.note-highlight {
  border: 2px solid #ffcc67fb;
}
.note-highlight .note-title {
  color: #ffcc67fb !important;
}

/* -------------------------------------------------------------------------- */
/*                                   NAVBAR                                   */
/* -------------------------------------------------------------------------- */
nav {
  background-color: #2A3947 !important;
  padding-top: 1em;
  padding-bottom: 1em;
}
nav .navbar-item:hover {
  color: #ffcc67fb;
}
nav .navbar {
  height: 120px !important;
}
nav img {
  width: 120px;
  height: 80px;
  max-height: 80px !important;
}
nav a:hover {
  color: white !important;
  text-decoration: none;
  transform: scale(1.02);
  transition: transform 0.25s ease-in-out;
}
nav a {
  transform: scale(1);
  transition: transform 0.25s ease-in-out;
}
nav .navbartitle {
  color: white !important;
  font-size: 60px;
  font-weight: 700;
  padding-left: 4rem;
}
nav .navbarpage {
  color: white !important;
  font-size: 1.4rem;
  font-weight: 700;
  margin-right: 2rem;
  font-family: Nunito Sans;
}
nav .navbarpage:hover {
  text-decoration: underline;
  transform: scale(1.1);
}
nav .BCII-name-navbar {
  font-size: 6rem;
  line-height: 4rem;
  font-weight: 900;
  padding-left: 2rem;
  color: white;
  font-family: DM Serif Display;
}

/* -------------------------------------------------------------------------- */
/*                                  materials                                 */
/* -------------------------------------------------------------------------- */
.materials-place-holder {
  height: 100vh;
  background-image: url(../imgs/patterns/diagonal-lines.svg);
  background-repeat: repeat;
  background-position: center center;
  padding: 5rem;
  font-size: 2rem;
}

/* -------------------------------------------------------------------------- */
/*                                EVENT SUMMARY                               */
/* -------------------------------------------------------------------------- */
.event-summary {
  background-color: #1E2A35 !important;
  min-height: 100px;
  padding: 2rem 10% 4rem 10%;
}
.event-summary span {
  font-size: 1.5rem;
  font-weight: 900;
}
.event-summary .fancy-title {
  text-align: center;
  margin: 0 auto 2rem auto;
  width: 100%;
}
.event-summary .is-child {
  color: white;
  font-family: Nunito Sans;
  font-size: 1.2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 1rem;
  border-radius: 12px;
}
.event-summary .is-img-tile {
  border: none;
}
.event-summary .event-photo {
  border-radius: 4px !important;
  border: 1px solid #aabfd1;
}
.event-summary .is-photos-tile {
  background-color: #1E2A35 !important;
  border: none !important;
  padding: 0 !important;
  .is-parent-padding: 0px !important;
  .is-parent-margin: 0px !important;
}
.event-summary .is-photos-tile .tile {
  background-color: #1E2A35 !important;
  border: none !important;
}

/* -------------------------------------------------------------------------- */
/*                                   IMPORTS                                  */
/* -------------------------------------------------------------------------- */
/* -------------------------------------------------------------------------- */
/*                                   LANDING                                  */
/* -------------------------------------------------------------------------- */
.landing-hero {
  height: 100vh;
  background-image: url(../imgs/patterns/topography.svg);
  background-repeat: repeat;
  background-position: center center;
  background-size: cover;
}
.landing-hero .landing-hero-content {
  max-width: 1600px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.landing-hero .landing-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.landing-hero .BCII-name {
  font-size: 12rem;
  line-height: 12rem;
  font-weight: 900;
  color: white;
  font-family: DM Serif Display;
  z-index: 2;
  text-align: center;
  margin-top: 5vh;
  display: block;
  position: relative;
}
@media screen and (min-width: 1600px) {
  .landing-hero .BCII-name {
    font-size: 15rem;
    line-height: 15rem;
  }
}
@media screen and (max-width: 600px) {
  .landing-hero .BCII-name {
    width: 95%;
    font-size: 3.5rem;
    line-height: 5rem;
  }
}
.landing-hero .BCII-slogan {
  font-size: 3rem;
  font-family: Rosario;
  text-align: center;
  line-height: 5rem;
  color: white;
  position: relative;
  z-index: 2;
  opacity: 0;
  transition: opacity 0.5s, transform 1s;
}
@media screen and (max-width: 600px) {
  .landing-hero .BCII-slogan {
    font-size: 2rem;
    width: 90%;
    line-height: 2.5rem;
    margin-bottom: 2em;
  }
}
.landing-hero .landing-hero-img {
  max-width: 40vh;
  z-index: 0;
  opacity: 0;
}
@media screen and (min-width: 1600px) {
  .landing-hero .landing-hero-img {
    max-width: 22em;
    margin-top: 8em;
  }
}
@media screen and (max-width: 600px) {
  .landing-hero .landing-hero-img {
    z-index: 0;
    margin-top: 2em;
  }
}
.landing-hero .scroll-down {
  position: absolute;
  width: 50px;
  height: 50px;
  left: 50%;
  transform: translate(-60%, 0);
  bottom: 1em;
}
@media screen and (min-width: 1600px) {
  .landing-hero .scroll-down {
    width: 105px;
    height: 105px;
  }
}
@media screen and (max-width: 600px) {
  .landing-hero .scroll-down {
    width: 50px;
    height: 50px;
  }
}

.landing-content {
  max-width: 1600px;
  margin: 0 auto;
  padding-bottom: 1em;
  width: 90%;
}

.aim-num {
  font-size: 5rem;
  font-weight: 900;
  font-family: Teko;
  display: inline-block;
  margin-right: 0.5em;
  padding: 0;
  height: 100px;
  line-height: 100px;
}
@media screen and (max-width: 600px) {
  .aim-num {
    font-size: 2rem;
  }
}

/* ---------------------------------- event --------------------------------- */
.event-link {
  text-decoration: none !important;
  color: white !important;
}
.event-link .event-info {
  text-decoration: none !important;
}
.event-link:hover .event {
  transform: scale(1.02);
  transition: transform 0.25s ease-in-out;
  box-shadow: 0 0 20px 10px rgba(0, 0, 0, 0.1);
}
.event-link:hover .event .event-img {
  transform: translateX(20px);
  transform: scale(1.1);
  transition: transform 0.25s ease-in-out;
}
.event-link .event {
  transform: scale(1);
  transition: transform 0.25s ease-in-out;
  box-shadow: none;
}
.event-link .event .event-img {
  transform: translateX(0px);
  transform: scale(1);
  transition: transform 0.25s ease-in-out;
}

.event-green {
  background-color: #6bce70;
  border: 2px solid #6bce70;
}
.event-green .event-title {
  color: #6bce70;
}

.event-blue {
  background-color: #58b4ff;
  border: 2px solid #58b4ff;
}
.event-blue .event-title {
  color: #58b4ff;
}

.event-red {
  background-color: #fb835f;
  border: 2px solid #fb835f;
}
.event-red .event-title {
  color: #fb835f;
}

.event-yellow {
  background-color: #ffcc67fb;
  border: 2px solid #ffcc67fb;
}
.event-yellow .event-title {
  color: #ffcc67fb;
}

.event {
  width: 80%;
  margin: 0 auto;
  margin-bottom: 2em;
  border-radius: 10px;
  padding: 1em 10% 1.5em 3em;
  background-color: #1E2A35;
  position: relative;
  overflow: hidden;
}
@media screen and (max-width: 600px) {
  .event {
    width: 95%;
    padding: 1em;
  }
}
.event .event-img {
  position: absolute;
  bottom: -20px;
  right: 0;
  border-radius: 10px;
  width: 20%;
}
.event .event-img img {
  object-fit: contain;
  width: 100%;
  height: 100%;
  border-radius: 15px;
}
@media screen and (max-width: 600px) {
  .event .event-img {
    width: 60%;
    bottom: -5px;
    height: 80%;
    right: -50px;
  }
}
.event .event-title {
  font-size: 2.5rem;
  font-weight: 900;
  margin-bottom: 0.5em;
  font-family: Teko;
  position: relative;
}
@media screen and (max-width: 600px) {
  .event .event-title {
    font-size: 1.7rem;
  }
}
.event .event-description {
  font-family: Teko;
  font-size: 1.5rem;
  font-weight: 900;
  margin-bottom: 0.5em;
  font-family: Space Mono;
  color: white;
  position: relative;
}
@media screen and (max-width: 600px) {
  .event .event-description {
    font-size: 1.2rem;
  }
}
.event .event-info {
  margin-top: 1.5em;
  display: flex;
  font-family: Space Mono;
  margin-bottom: 0;
}
@media screen and (max-width: 600px) {
  .event .event-info {
    font-size: 0.8rem;
    text-align: center;
    width: 95%;
  }
  .event .event-info .column {
    padding: 0;
  }
}
.event .espace {
  margin-left: 24px;
  margin-right: -8px;
}
.event .columns {
  width: 95% !important;
}

.event-placeholder {
  opacity: 0.5 !important;
  border: 2px solid #b4b4b4;
}
.event-placeholder .event-title {
  color: white !important;
  opacity: 1 !important;
  font-size: 2.5rem;
  margin: 0 !important;
  font-family: Teko;
}
@media screen and (max-width: 600px) {
  .event-placeholder .event-title {
    font-size: 1.7rem !important;
  }
}

/* ---------------------------------- team ---------------------------------- */
.team {
  margin-top: 1em;
  margin-bottom: 6em;
}

.team-member {
  background-color: white;
  width: 250px;
  height: 350px;
  border-radius: 10px;
  background-color: #2A3947;
  margin: 0 auto;
  padding: 1em;
}
.team-member .team-member-name {
  width: 100%;
  height: 20%;
  z-index: 1;
  padding: 10px;
  font-family: Nunito Sans;
  font-size: 1.25rem;
  font-weight: 900;
  color: white;
  text-align: center;
}
.team-member .team-member-role {
  color: #fb835f !important;
  text-decoration: underline !important;
  font-size: 1.4rem !important;
}
.team-member .team-member-img {
  width: 100%;
  height: 80%;
  border-radius: 15px;
  opacity: 1;
  background-color: white;
}
.team-member .team-member-img img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  border-radius: 15px;
  border: 2px solid #c8c8c8;
}

/* -------------------------------- partners -------------------------------- */
.partners {
  margin-bottom: 6em;
}

/* --------------------------------- signup --------------------------------- */
.landing-signup {
  margin: 4em 0 0em 0;
  background-color: white;
  color: black;
  width: 100vw;
  padding: 2em 4em 2em 4em;
  background-image: url(../imgs/patterns/topography_25.svg);
}
@media screen and (max-width: 600px) {
  .landing-signup {
    padding: 2.5% 2em 2.5% 2em;
    text-align: center;
  }
}
.landing-signup .signup-text {
  font-size: 2rem;
  font-family: Cormorant Upright;
  margin-bottom: 0.5em;
}
@media screen and (max-width: 600px) {
  .landing-signup .signup-text {
    font-size: 1.5rem;
  }
}
.landing-signup .form-placeholder {
  background-color: #e6e6e6;
  height: 50px;
  border-radius: 25px;
  border: 1px solid #646464;
  margin-top: 2em;
  margin-bottom: 2em;
}

/* ------------------------------- signup form ------------------------------ */
#mc_embed_signup {
  background: none !important;
}

@media screen and (max-width: 600px) {
  .mc-embedded-subscribe-form {
    margin: 0px !important;
  }
}

.mc-field-group {
  width: 100% !important;
}
.mc-field-group label {
  font-family: Nunito Sans;
  font-size: 1rem;
  font-weight: 900;
  padding-left: 2rem;
}
.mc-field-group input {
  font-family: Nunito Sans;
  font-size: 1rem;
